}
/* 소개 타이틀 박스 h1태그 */
.abu-title-bx h1 {
    font-size: 6.6rem;
    font-weight: bold;
    font-family: "Lora", serif;
    padding: 33px 0;
}
/* about us 메인 비주얼 컨텐츠 플렉스박스 공통 */
.flex-cont-bx {
    display: flex;
    align-items: center;
    justify-content: center;
