    font-family: "Noto Sans KR", sans-serif;
    font-weight: bold;
    line-height: 1.4;
    padding-bottom: 30px;
}
/* 텍스트 박스 p */
.txt-bx p {
    font-size: 1.1rem;
    font-family: "Noto Sans KR", sans-serif;
    font-weight: bold;
    line-height: 1.6;
    font-weight: bold;
}

/* 타이틀 이미지 박스1 위치 */
