/**************************************** 5. 꼬띠샵 마스코트 소개영역 ****************************************/
/* 전체싸는 속박스 */
.abu-who-inbox {
    max-width: 1650px;
    margin: 0 auto;
    height: 1500px;
    margin-bottom: 102px;
    box-sizing: border-box;
}
/* 강아지 gif 박스 */
.puppy-img-bx {
    position: relative;
    top: 244px;
    left: -11%;
}
/* 레이어 이미지 박스 가운데 정렬 */
.about-section__title figure {
    text-align: center;
    padding-top: 100px;
}
.about-section__title h2 {
    text-align: center;
    font-size: 6.1rem;
    font-weight: bold;
    padding-bottom: 40px;
}
/* 프로필 b태그 */
.about-who__profile b {
    text-align: center;
    display: block;
    font-size: 2.2rem;
    /* padding: 10px 0; */
    padding-top: 33px;
    padding-bottom: 20px;
}
ul.profile-txt {
    text-align: center;
}

ul.profile-ani {
    display: flex;
    align-items: center;
    justify-content: center;

    padding-top: 125px;
}
ul.profile-txt {
    position: relative;
}

ul.profile-txt li {
    padding: 13px 0;
}
/* 가상요소 밑줄 */
ul.profile-txt li:nth-of-type(1):before {
    content: "";
    display: block;
    width: 480px;
    height: 8px;
    position: absolute;
    left: 35.7%;
    top: -4px;
    background: url(../../public/images/aboutus/cotie-profile-line.jpg) no-repeat center/cover;
}
ul.profile-txt li:nth-of-type(2):before {
    content: "";
    display: block;
    width: 480px;
    height: 8px;
    left: 35.7%;
    top: 15.5%;
    position: absolute;

    background: url(../../public/images/aboutus/cotie-profile-line.jpg) no-repeat center/cover;
}
ul.profile-txt li:nth-of-type(3):before {
    content: "";
    display: block;
    width: 480px;
    height: 8px;
    left: 35.7%;
    top: 32.2%;
    position: absolute;

    background: url(../../public/images/aboutus/cotie-profile-line.jpg) no-repeat center/cover;
}
ul.profile-txt li:nth-of-type(4):before {
    content: "";
    display: block;
    width: 480px;
    height: 8px;
    left: 35.7%;
    top: 49%;
    position: absolute;

    background: url(../../public/images/aboutus/cotie-profile-line.jpg) no-repeat center/cover;
}
ul.profile-txt li:nth-of-type(5):before {
    content: "";
    display: block;
    width: 480px;
    height: 8px;
    left: 35.7%;
    top: 65.5%;
    position: absolute;

    background: url(../../public/images/aboutus/cotie-profile-line.jpg) no-repeat center/cover;
}
ul.profile-txt li:nth-of-type(6):before {
    content: "";
    display: block;
    width: 480px;
    height: 8px;
    left: 35.7%;
    top: 82.4%;
    position: absolute;

    background: url(../../public/images/aboutus/cotie-profile-line.jpg) no-repeat center/cover;
}
ul.profile-txt li:nth-of-type(6)::after {
    content: "";
    display: block;
    width: 480px;
    height: 8px;
    left: 35.7%;
    top: 98.7%;
    position: absolute;

    background: url(../../public/images/aboutus/cotie-profile-line.jpg) no-repeat center/cover;
}
ul.profile-txt span {
    display: inline-block;
    font-size: 1.7rem;
}
ul.profile-ani {
    /* position: relative; */
    /* width: 100%;
    list-style: none;
    overflow: hidden;
    white-space: nowrap; */
}

/* 마스코트 이미지 박스 */
ul.profile-ani li {
    display: inline-block;
    margin-right: 20px; /* 이미지 간격 조정 */
    opacity: 0; /* 초기에는 이미지 숨김 */
    transition: opacity 0.5s ease, transform 0.5s ease; /* 투명도 애니메이션 설정 */
}
.item1 {
    /* 첫 번째 이미지에 적용될 스타일 */
    transform: translateX(10px) rotate(-20deg);
}

.item2 {
    /* 두 번째 이미지에 적용될 스타일 */
    /* transform: translateY(50px) rotate(180deg); */
}

.item3 {
    /* 세 번째 이미지에 적용될 스타일 */
    transform: translateX(10px) rotate(20deg);
}

